.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 100%;
  color: white;
}

.App-header-Hidden {
  visibility: hidden;
  display: none;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

.iconMenu {
  float: left;
  margin-bottom: 20px;
}

.input {
  color: white;
}

.search-bar {
  text-align: center;
}

.layerOverVideo {
  left: 0;
  top: 0;
  z-index: -1;
  position: absolute;
  width: 100%;
  height: 75%;
}


.embed-responsive-item {
  left: 0;
  top: 0;
  z-index: -2;
  position: absolute;
  height: 100%;
  width: 100%;
}

.video-item-img {
  max-width: 64px;
}

.details {
  background-color: #222;
  color: white;
  border: 1px solid #ddd;
  border-radius: 4px;
  padding: 10px;
  margin-top: 10px;
}

.video-detail .details {
}

.video-list-media {
  padding: 10px;
}

.list-group-item {
  cursor: pointer;
}

.list-group-item:hover {
  background: #333;
}



